import { Button, Modal } from "@mui/material";
import { useState } from "react";
import "./Adicionales.css";

const Adicionales = () => {
  const [open, setOpen] = useState(false);
  const [open2, setOpen2] = useState(false);
  const [open3, setOpen3] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen2 = () => {
    setOpen2(true);
  };

  const handleClose2 = () => {
    setOpen2(false);
  };

  const handleOpen3 = () => {
    setOpen3(true);
  };

  const handleClose3 = () => {
    setOpen3(false);
  };

  return (
    <div className="container-adic">
      <div className="card-adic">
        <i class="bx bx-credit-card"></i>
        <h4>Eleg como pagar</h4>
        <p>
          Pods pagar con tarjeta, dbito, efectivo o con PayPal.
        </p>
        <Button onClick={handleOpen3}>Cmo pagar tus compras</Button>
      </div>

      <div className="card-adic">
        <i class="bx bxs-package"></i>
        <h4>Envo gratis desde $ 4.000</h4>
        <p>
          Solo por estar registrado en Shopty tens envos gratis en
          miles de productos.
        </p>
        <Button onClick={handleOpen2}>Conoc ms sobre este beneficio</Button>
      </div>

      <div className="card-adic-end">
        <i class="bx bx-check-shield"></i>
        <h4>Seguridad, de principio a fin</h4>
        <p>
          No te gusta? Devolvelo! En Shopty, no hay nada que no puedas
          hacer, porque ests siempre protegido.
        </p>
        <Button onClick={handleOpen}>Cmo te protegemos</Button>
      </div>

      <Modal open={open} onClose={handleClose}>
        <div className="modal-container">
          <div className="form-modal">
            <h3>Cmo te protegemos?</h3>
            <h5>No te gusta lo que compraste?</h5>
            <h6>Devolvelo Gratis!</h6>
            <p>Sos libre de cambiar de opinin</p>
            <div className="row-modal">
              <div className="card-modal">
                <div className="circle-item">
                  <i class="bx bx-check-square"></i>
                </div>
                <h6>Cuidamos tus datos</h6>
              </div>
              <div className="card-modal">
                <div className="circle-item">
                  <i class="bx bx-credit-card-front"></i>
                </div>
                <h6>Protejemos tu dinero</h6>
              </div>
              <div className="card-modal">
                <div className="circle-item">
                  <i class="bx bx-package"></i>
                </div>
                <h6>Monitoreamos tu envo</h6>
              </div>
            </div>
            <Button variant="outlined" onClick={handleClose}>
              Cerrar
            </Button>
          </div>
        </div>
      </Modal>

      <Modal open={open2} onClose={handleClose2}>
        <div className="modal-container">
          <div className="form-modal">
            <h3>Envos gratis desde $4.000!</h3>
            <h5>
              Si! Disponemos de envio gratis en productos apartir de $4.000!
            </h5>
            <h6>Qu esperas?</h6>
            <p>Hac tu pedido ya mismo!</p>
            <div className="row-modal">
              <div className="card-modal">
                <div className="circle-item">
                  <i class="bx bxs-badge-dollar"></i>
                </div>
                <h6>No te lo pierdas!</h6>
              </div>
            </div>
            <Button variant="outlined" onClick={handleClose2}>
              Cerrar
            </Button>
          </div>
        </div>
      </Modal>

      <Modal open={open3} onClose={handleClose3}>
        <div className="modal-container">
          <div className="form-modal">
            <h3>Eleg el que ms te convenga</h3>
            <h5>
              Pag tus compras y suscripciones con cualquiera de estos medios{" "}
            </h5>
            <div className="row-modal">
              <div className="card-modal">
                <div className="circle-item">
                  <i class="bx bx-dollar"></i>
                </div>
                <h6>Prefers pagar en efectivo?</h6>
                <p>
                  Cuando compres, te daremos un cdigo para que puedas pagar en
                  cualquier sucursal.
                </p>
              </div>
              <div className="card-modal">
                <div className="circle-item">
                  <i class="bx bxs-credit-card-front"></i>
                </div>
                <h6>Tarjeta de crdito o dbito</h6>
                <p>
                  Pag en cuotas sin inters o hacelo al contado con tu tarjeta
                  de dbito.
                </p>
              </div>
              <div className="card-modal">
                <div className="circle-item">
                  <i class="bx bxs-bank"></i>
                </div>
                <h6>Transfer desde tu homebanking</h6>
                <p>
                  Es tan simple que pods hacerlo desde el silln de tu casa.
                </p>
              </div>
              <div className="card-modal">
                <div className="circle-item">
                  <i class="bx bxl-paypal"></i>
                </div>
                <h6>Dinero en tu cuenta de PayPal</h6>
                <p>
                  Ingres dinero en tu cuenta con cualquier medio de pago en
                  efectivo y cuando pagus tu compra, la acreditacin ser
                  instantnea.
                </p>
              </div>
            </div>
            <Button variant="outlined" onClick={handleClose3}>
              Cerrar
            </Button>
          </div>
        </div>
      </Modal>
    </div>
  );
};

export default Adicionales;
